.navbar {
    width: 100%;
    height: 130px;
    display: flex;
    justify-content: space-between;
}
.navbar__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
}
.navbar__logo a {
   text-decoration: none;
   font-size: 20px;
   font-weight: 900;
}
.navbar__menu {
    display: flex;
    align-items: center;
    gap: 10px;
}
.navbar__menu a{
text-decoration: none;
color: #f0f0f0;
}
.navbar__enter {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-right: 10px;
}
.navbar__enter .enter-login a{
    color: #00a0df;
    margin-right: 5px;
    text-decoration: none;

}
.navbar__enter .enter-reg a{
    color: #dfcf00;
    margin-left: 5px;
    text-decoration: none;

}
